.touch-info {
  background-color: #000000;
  padding: 20px;
  border-radius: 8px;
  width: 460px;
  height: auto;
  margin-bottom: 20px;
}

.touch-info h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #FFFFFF;
}

.touch-bar {
  display: flex;
  align-items: center;
  margin: 5px 0;
  color: #FFFFFF;
}

.touch-bar span {
  flex: 1;
}

.bar {
  flex: 4;
  height: 20px;
  border-radius: 10px;
  background-color: #333333;
  position: relative;
  margin: 0 10px;
}

.bar::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border: 1px solid #fff;
}
